import {
  applyMiddleware,
  combineReducers,
  createStore,
  compose,
} from 'redux';
import articlesReducer, { moduleName as articlesModuleName } from './articles';
import diabloHeroesReducer, { moduleName as diabloHeroesModuleName } from './diabloHeroes';
import axiosMiddleware from './axiosMiddleware';

const reducers = combineReducers({
  [articlesModuleName]: articlesReducer,
  [diabloHeroesModuleName]: diabloHeroesReducer,
});

const composeEnhancers = (<any> window).__REDUX_DEVTOOLS_EXTENSION_COMPOSE__ || compose;
const store = createStore(reducers, composeEnhancers(
  applyMiddleware(axiosMiddleware)
));

export default store;


