enum ASYNC_STATE {
  NOT_STARTED = 'NOT_STARTED',
  LOADING = 'LOADING',
  DONE = 'DONE',
  ERROR = 'ERROR',
}

export function isDone(state: ASYNC_STATE) {
  return state === ASYNC_STATE.DONE;
}

export function isError(state: ASYNC_STATE) {
  return state === ASYNC_STATE.ERROR;
}

export function isLoading(state: ASYNC_STATE) {
  return state === ASYNC_STATE.LOADING;
}

export default ASYNC_STATE;



