import * as React from 'react';
import { Link } from 'react-router-dom';

interface LayoutProps {
  children: React.ReactNode;
}

export default function Layout(props: LayoutProps) {
  return (
    <section className="layout">
      <header>
        <nav>
          <ul>
            <li className="home">
              <Link to="/">
                <span>Grant R. Klinsing</span>
              </Link>
            </li>
            <li className="github">
              <a href="http://github.com/kagd" target="_blank">
                <span>Github</span>
              </a>
            </li>
            <li className="twitter">
              <a href="http://twitter.com/gklinsing" target="_blank">
                <span>Twitter</span>
              </a>
            </li>
            <li className="linked-in">
              <a href="https://www.linkedin.com/in/grantklinsing" target="_blank">
                <span>LinkedIn</span>
              </a>
            </li>
          </ul>
        </nav>
      </header>

      <section className="main" id="main" role="main">
        {props.children}
      </section>
    </section>
  );
}


