export namespace Interfaces {
  export interface State {
    [x: number]: {};
  }
}

export function updateState(state: Interfaces.State, updates: {}): {} {
  const newState = {
    ...state,
    ...updates,
  };

  return newState;
}

interface Item {
  id: number | string;
}

export function setStateFromResponse<I extends Item>( state: {}, response: I[] ): {} {
  const stateClone = { ...state };

  const newState = response.reduce(function(memo, item) {
    memo[item.id] = item;
    return memo;
  }, stateClone);

  return newState;
}


