import ACTION_TYPES, { Interfaces } from './actionTypes';
import ASYNC_STATE from '../constants/asyncState';
import { createAxiosAction } from '../redux/axiosMiddleware';
import Hero from '../models/diabloHero';
import {
  Interfaces as StateInterfaces,
  setStateFromResponse,
  updateState,
} from './state';

const enum STATE_KEYS {
  HEROES = 'HEROES',
  LOADING = 'LOADING',
}

const initialState = {
  [STATE_KEYS.HEROES]: {},
  [STATE_KEYS.LOADING]: ASYNC_STATE.LOADING,
};

export const moduleName = 'diablo';

export const getHero = (state: StateInterfaces.State, id: string): Hero => state[moduleName][STATE_KEYS.HEROES][id];
export const getHeroes = (state: StateInterfaces.State): Hero[] => {
  const heroes = state[moduleName][STATE_KEYS.HEROES];
  const ids = Object.keys(heroes);
  return ids.map((id, i) => heroes[id]);
};
export const getLoadingState = (state: StateInterfaces.State): ASYNC_STATE => state[moduleName][STATE_KEYS.LOADING];

export default function diabloHeroesReducer(state = initialState, action: Interfaces.ActionType) {
  switch ( action.type ) {
    case ACTION_TYPES.DIABLO_HERO_FETCH_ALL:
      return updateState(state, {[STATE_KEYS.LOADING]: ASYNC_STATE.LOADING});
    case ACTION_TYPES.DIABLO_HERO_FETCH_ALL_RESPONSE:
      return updateState(state, {
        [STATE_KEYS.HEROES]: setStateFromResponse(state[STATE_KEYS.HEROES], action.data.heroes),
        [STATE_KEYS.LOADING]: ASYNC_STATE.DONE
      });
    case ACTION_TYPES.DIABLO_HERO_FETCH_ALL_ERROR:
      return updateState(state, {[STATE_KEYS.LOADING]: ASYNC_STATE.ERROR});
    default:
      return state;
  }
}

export function fetchDiabloHeroes() {
  return createAxiosAction({
    url: 'http://grantrklinsing.com/api/diablo',
    method: 'get',
    pendingActionType: ACTION_TYPES.DIABLO_HERO_FETCH_ALL,
    successActionType: ACTION_TYPES.DIABLO_HERO_FETCH_ALL_RESPONSE,
    failedActionType: ACTION_TYPES.DIABLO_HERO_FETCH_ALL_ERROR,
  });
}



