import {
  fetchArticle,
  getArticle,
  isLoading,
} from '../../redux/articles';
import { connect } from 'react-redux';
import { RouteComponentProps } from 'react-router';
import Article from './Article';

interface RouteParams {
  id: string;
}

function mapStateToProps(state: any, ownProps: RouteComponentProps<RouteParams>) {
  const id = ownProps.location.pathname;
  const article = getArticle(state, id);
  return {
    article,
    id,
    isLoading: isLoading(state),
  };
}

const mapDispatchToProps = {
  fetchArticle,
};

const ArticleContainer = connect(mapStateToProps, mapDispatchToProps)(Article);

export default ArticleContainer;


