import axios, { AxiosResponse } from 'axios';
import {
  AnyAction,
  Middleware,
} from 'redux';
import ACTION_TYPES from './actionTypes';

export const AxiosParams = Symbol('AxiosParams');
// visibleForTesting
export const AXIOS_ACTION = 'shared/middleware/axios';

export interface AxiosActionParams {
  url: string;
  method: 'get'|'post'|'put'|'delete';
  data?: any;
  pendingActionType: ACTION_TYPES;
  successActionType: ACTION_TYPES;
  failedActionType: ACTION_TYPES;
}

// visibleForTesting
export const INTERNALS = {
  axios,
};

export function createAxiosAction(options: AxiosActionParams): AnyAction {
  return {
    type: AXIOS_ACTION,
    [AxiosParams]: {
      ...options,
    },
  };
}

const axiosMiddleware: Middleware = ({ dispatch }) => (next) => (action: AnyAction) => {
  if (typeof action === 'object' && action.type === AXIOS_ACTION) {
    const params: AxiosActionParams = action[AxiosParams];
    const {
      data,
      method,
      url,
      pendingActionType,
      successActionType,
      failedActionType,
    } = params;

    // dispatch an action indicating a pending API call
    dispatch({
      type: pendingActionType,
    });

    // we need to make the call and then dispatch the correct action types
    const requestParams = {
      data,
      method,
      url,
    };
    INTERNALS.axios.request(requestParams).then((response: AxiosResponse) => {
      dispatch({
        type: successActionType,
        data: response.data,
      });
    }, (error: any) => {
      dispatch({
        type: failedActionType,
        data,
        error,
      });
    });
  }

  // if it is not a axios action then allow it to proceed
  return next(action);
};

export default axiosMiddleware;



