import ACTION_TYPES, { Interfaces } from './actionTypes';
import ASYNC_STATE, { isLoading as asyncStateIsLoading } from '../constants/asyncState';
import { createAxiosAction } from '../redux/axiosMiddleware';
import ArticleModel from '../models/article';
import {
  Interfaces as StateInterfaces,
  setStateFromResponse,
  updateState,
} from './state';

const enum STATE_KEYS {
  ARTICLES = 'ARTICLES',
  LOADING = 'LOADING',
}

const initialState = {
  [STATE_KEYS.ARTICLES]: {},
  [STATE_KEYS.LOADING]: ASYNC_STATE.LOADING,
};

export const moduleName = 'articles';

export const getArticle = (state: StateInterfaces.State, id: string): ArticleModel => state[moduleName][STATE_KEYS.ARTICLES][id];
export const getArticles = (state: StateInterfaces.State): ArticleModel[] => {
  const articlesObj = state[moduleName][STATE_KEYS.ARTICLES] as typeof initialState[STATE_KEYS.ARTICLES];
  const articleIds = Object.keys(articlesObj);
  const articles = articleIds.map((id, i, arr) => {
    return articlesObj[id];
  });

  return articles;
};
export const getLoadingState = (state: StateInterfaces.State): ASYNC_STATE => state[moduleName][STATE_KEYS.LOADING];
export const isLoading = (state: StateInterfaces.State) => asyncStateIsLoading(getLoadingState(state));

export default function articlesReducer(state = initialState, action: Interfaces.ActionType) {
  switch ( action.type ) {
    case ACTION_TYPES.ARTICLES_FETCH:
    case ACTION_TYPES.ARTICLES_FETCH_ALL:
      return updateState(state, {[STATE_KEYS.LOADING]: ASYNC_STATE.LOADING});
    case ACTION_TYPES.ARTICLES_FETCH_ALL_RESPONSE:
      return updateState(state, {
        [STATE_KEYS.ARTICLES]: setStateFromResponse(state[STATE_KEYS.ARTICLES], action.data),
        [STATE_KEYS.LOADING]: ASYNC_STATE.DONE
      });
    case ACTION_TYPES.ARTICLES_FETCH_RESPONSE:
      return updateState(state, {
        [STATE_KEYS.ARTICLES]: setStateFromResponse(state[STATE_KEYS.ARTICLES], [action.data]),
        [STATE_KEYS.LOADING]: ASYNC_STATE.DONE
      });
    case ACTION_TYPES.ARTICLES_FETCH_ERROR:
    case ACTION_TYPES.ARTICLES_FETCH_ALL_ERROR:
      return updateState(state, {[STATE_KEYS.LOADING]: ASYNC_STATE.ERROR});
    default:
      return state;
  }
}

export function fetchArticles() {
  const action = createAxiosAction({
    url: '/articles/list.json',
    method: 'get',
    pendingActionType: ACTION_TYPES.ARTICLES_FETCH_ALL,
    successActionType: ACTION_TYPES.ARTICLES_FETCH_ALL_RESPONSE,
    failedActionType: ACTION_TYPES.ARTICLES_FETCH_ALL_ERROR,
  });

  return action;
}

export function fetchArticle(url: string) {
  const action = createAxiosAction({
    url: `${url}.json`,
    method: 'get',
    pendingActionType: ACTION_TYPES.ARTICLES_FETCH,
    successActionType: ACTION_TYPES.ARTICLES_FETCH_RESPONSE,
    failedActionType: ACTION_TYPES.ARTICLES_FETCH_ERROR,
  });

  return action;
}



